@import "colors";

.small {
  padding: 10px 18px;
  font-size: 12px;
  line-height: 12px;
}

.medium {
  font-size: 16px;
  line-height: 16px;
  padding: 13px 24px;
}

.large {
  padding: 16px 48px;
  font-size: 16px;
  line-height: 16px;
}

.contentSize {
  margin: 0 auto;
  width: 1280px;
  max-width: 90%;
  box-sizing: border-box;

  @media only screen and (max-width: 1440px) {
    width: 1280px;
  }
}